гирования и получают\n        эритроцитарную массу, которую можно\n        хранить в специальном холодильнике в\n        течение 21-35-42 дней в зависимости\n        от консерванта.")]), _vm._v(" "), _c('p', [_vm._v("        Параллельно с процессом\n        центрифугирования, кровь в лаборатории\n        еще раз проверяют на группу крови и\n        резус-фактор, тестируют на инфекции,\n        передающиеся через кровь, делают\n        биохимический и общий анализы крови.\n        Если не выявят противопоказаний\n        анализы, кровь после обработки смогут\n        выдать для переливания реципиенту.")]), _vm._v(" "), _c('p', [_vm._v("      Полезная информация для донора, с\n        которой стоит ознакомиться\n        ОБЯЗАТЕЛЬНО перед донацией:")]), _vm._v(" "), _vm._v("\n    Что происходит, когда я сдаю кровь?\n    "), _c('router-link', {
